import React, { useState, useEffect } from "react";
import axios from "axios";
import SpotifyPlayer from "react-spotify-web-playback";
import styled from "styled-components";

//const BACKEND_URL = "https://spotify-rec-axk8.onrender.com";
const BACKEND_URL = "http://localhost:5000";

// 🌟 Styled Components for Better UI
const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #191414;
  color: white;
  font-family: Arial, sans-serif;
  text-align: center;
`;

const Title = styled.h1`
  font-size: 2rem;
  margin-bottom: 20px;
`;

const ButtonContainer = styled.div`
  display: flex;
  gap: 20px;
  margin-top: 20px;
`;

const BubbleButton = styled.button`
  background-color: #1db954;
  color: white;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 15px 30px;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);

  &:hover {
    background-color: #1ed760;
    transform: scale(1.05);
  }

  &:active {
    transform: scale(0.95);
  }
`;

const App = () => {
  const [token, setToken] = useState(null);
  const [playlistUrl, setPlaylistUrl] = useState("");

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const accessToken = urlParams.get("access_token");
    if (accessToken) {
      setToken(accessToken);
      localStorage.setItem("spotify_access_token", accessToken);
    } else {
      const storedToken = localStorage.getItem("spotify_access_token");
      if (storedToken) setToken(storedToken);
    }
  }, []);

  const handleLogin = () => {
    window.location.href = `${BACKEND_URL}/login`;
  };

  const generatePlaylist = async () => {
    try {
      const response = await axios.get(`${BACKEND_URL}/liked-songs`, {
        headers: { Authorization: token },
      });

      const { data } = await axios.post(`${BACKEND_URL}/generate-playlist`, {
        likedSongs: response.data,
        accessToken: token,
      });

      setPlaylistUrl(data.playlistUrl);
    } catch (error) {
      console.error("Error generating playlist:", error);
    }
  };

  return (
    <AppContainer>
      <Title>🎵 Spotify AI Playlist Generator 🎵</Title>

      {/* Login and Generate Buttons are Always Visible */}
      <ButtonContainer>
        <BubbleButton onClick={handleLogin}>Login with Spotify</BubbleButton>
        <BubbleButton onClick={generatePlaylist}>Generate Playlist</BubbleButton>
      </ButtonContainer>

      {playlistUrl && <SpotifyPlayer token={token} uris={[playlistUrl]} />}
    </AppContainer>
  );
};

export default App;
